
import React, { useState, useEffect } from 'react';
import classNames from 'classnames';

function Titles() {

	const transitionsTitle = [
		{ id: 0, label: 'L Lower 3rd' },
		{ id: 1, label: 'M Lower 3rd' },
		{ id: 2, label: 'R Lower 3rd' },
		{ id: 3, label: 'Scroll' },
		{ id: 4, label: 'Text' },
		{ id: 5, label: 'Text+' },
		{ id: 6, label: '3D Lower 3rd Flipping 2Line' },
		{ id: 7, label: '3D Lower 3rd Multiplane Background' },
		{ id: 8, label: '3D Lower 3rd Plane Behind Slide In' },
		{ id: 9, label: '3D Lower 3rd Plane Slide In' },
		{ id: 10, label: '3D Lower 3rd Planes Rotating In' },
		{ id: 11, label: '3D Lower 3rd Rotating Plane 2 Line' },
		{ id: 12, label: '3D Lower 3rd Shelf' },
		{ id: 13, label: '3D Lower 3rd Sliding Block' },
		{ id: 14, label: '3D Lower 3rd Text In Tube' },
		{ id: 15, label: '3D Title Backlit Rise' },
		{ id: 16, label: '3D Title Big And Small Text' },
		{ id: 17, label: '3D Title Center Line' },
		{ id: 18, label: '3D Title Cinematic Type' },
		{ id: 19, label: '3D Title Dramatic Mood' },
		{ id: 20, label: '3D Title In A Box' },
		{ id: 21, label: '3D Title Motion Background' },
		{ id: 22, label: '3D Title Neon Outlines' },
		{ id: 23, label: '3D Title Outline Offset' },
		{ id: 24, label: '3D Title Reflective Type' },
		{ id: 25, label: '3D Title Spin Twist Drift' },
		{ id: 26, label: '3D Title Strech Zoom' },
		{ id: 27, label: '3D Title Superhero Zoom' },
		{ id: 28, label: '3D Title Text Circling Text' },
		{ id: 29, label: 'Lower 3rd Corporate Corners 1 Line' },
		{ id: 30, label: 'Lower 3rd Corporate Corners 2 Lines' },
		{ id: 31, label: 'Lower 3rd Draw On Two Line' },
		{ id: 32, label: 'Lower 3rd Simple Box 1 Line' },
		{ id: 33, label: 'Lower 3rd Simple Box 2 Lines' },
		{ id: 34, label: 'Lower 3rd Simple Box Thin 1 Line' },
		{ id: 35, label: 'Lower 3rd Simple Underline' },
		{ id: 36, label: 'Lower 3rd Simple Vertical 1 Line' },
		{ id: 37, label: 'Lower 3rd Simple Vertical 2 Lines' },
		{ id: 38, label: 'Title Draw On Corners 1 Line' },
		{ id: 39, label: 'Title Draw On Corners 2 Lines' },
		{ id: 40, label: 'Title Horizontal Line Reveal' },
		{ id: 41, label: 'Title Horizontal Slide' },
		{ id: 42, label: 'Title Rise Fade' },
		{ id: 43, label: 'Title Slide From Center Line' },
		{ id: 44, label: 'Title Square Line Tilting Text' },
		{ id: 45, label: 'Title Text Ripple' },
		{ id: 46, label: 'Title Threee Line Drop' },
		{ id: 47, label: 'Title Two Line Twist Reveal' },
		{ id: 48, label: 'Title Zipper' },
	];

	//==> SEARCH TERM

	const [searchTerm, setSearchTerm] = React.useState("");
	const [searchResults, setSearchResults] = React.useState([]);

	const handleChange = e => {
	  setSearchTerm(e.target.value);
	};

	React.useEffect(() => {
		const results = transitionsTitle.filter(transition =>
			transition.label.toLowerCase().includes(searchTerm)
		);
		setSearchResults(results);
	}, [searchTerm]);

	//==> END


	//==> SELECTING INDEX OF EACH LIST

	const [ transTitle, setTransTitle ] = useState(-1);
	const [ transFusionTitle, setTransFusionTitle ] = useState(-1);

	function handleTransTitle(i) {
		setTransTitle(i);
		setTransFusionTitle(-1);
	}

	function handleTransFusionTitle(i) {
		setTransFusionTitle(i);
		setTransTitle(-1);
	}

	//==> END

	return (
		<>
			{/* Mini Header */}
			<div className="MiniHeader">
				<div></div>

				<div></div>

				<div>
					<input
						type="text"
						placeholder="Search"
        				value={searchTerm}
						onChange={handleChange}
						style={{'width':'128px', 'margin': '0 10px 0 0'}}/>
					<div className="icon contextmenu borderL" style={{'width':'38px'}}></div>
				</div>
			</div>

			{/* Content */}
			<div className="TitlesContent">
				<div className="title">Titles</div>
				<div className="content">
					{searchResults.length > 0 ?
						<>
							{searchResults.filter(transition => transition.id < 6).map((transition, i) => (
								<div key={i} className={classNames('row', transTitle === i ? 'active':'')} onClick={()=> handleTransTitle(i)}>{transition.label}</div>
							))}
						</>
						:
						<div className="row">No results founded.</div>
					}
				</div>

				<div className="title">Fusion Titles</div>
				<div className="content">
					{searchResults.length > 0 ?
						<>
							{searchResults.filter(transition => transition.id > 6).map((transition, i) => (
								<div key={i} className={classNames('row', transFusionTitle === i ? 'active':'')} onClick={()=> handleTransFusionTitle(i)}>{transition.label}</div>
							))}
						</>
						:
						<div className="row">No results founded.</div>
					}
				</div>
			</div>

			{/* Viewer ToolBar */}
			<div className="editTools">
				<div>
					<div className="boring"></div>
					<div className="split"></div>
				</div>

				<div>
					<div className="insert"></div>
					<div className="to_end"></div>
					<div className="ripple_overwrite"></div>
					<div className="closeup"></div>
					<div className="put_on_top"></div>
					<div className="source_write"></div>
				</div>

				<div></div>
			</div>
		</>
	)
}

export default Titles;
